import { Controller, Inline } from '@ali/4ever-cangjie';

export const getSearchResult = (
  controller: Controller,
  findText: string,
  node: Inline,
) => {
  // TODO: 这里为什么可能为空？
  if (!node) return undefined;

  const {
    type,
    key,
    data: { name },
  } = node;
  if (type === 'mention' && `@${name}`.includes(findText)) {
    return { type, key, readonly: true };
  }
  return undefined;
};
